<style lang="less" rel="stylesheet/less">
#buttonForm {
    .simpleColorChooser {
        z-index: 98;
    }
}
</style>
<template>
  <form id="buttonForm">
    <div class="form-group row">
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               v-model="$parent.activeItem.text" />
      </div>
    </div>
    <div class="form-group">
      <label></label><br>
      <button type="button"
              class="btn btn-outline-secondary"
              :class="{'active': $parent.activeItem.btnType=='link'}"
              @click="changeBtnType('link')">
        
      </button>
      <button type="button"
              class="btn btn-outline-secondary"
              :class="{'active': $parent.activeItem.btnType=='tel'}"
              @click="changeBtnType('tel')">
        
      </button>
      <button type="button"
              class="btn btn-outline-secondary"
              :class="{'active': $parent.activeItem.btnType=='share'}"
              @click="changeBtnType('share')">
        
      </button>
    </div>
    <div class="form-group">
      <label v-show="$parent.activeItem.btnType=='link'"></label>
      <label v-show="$parent.activeItem.btnType=='tel'"></label>
      <input type="text"
             class="form-control"
             placeholder=""
             v-model="$parent.activeItem.href"
             v-show="$parent.activeItem.btnType!='share'" />
    </div>
    <div class="form-group row">
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control colorInput"
               placeholder=""
               v-model="$parent.activeItem.color" />
      </div>
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control bgColorInput"
               placeholder=""
               v-model="$parent.activeItem.bgColor" />
      </div>
    </div>
    <div class="form-group">
      <label></label>
      <input type="text"
             class="form-control borderColorInput"
             placeholder=""
             v-model="$parent.activeItem.borderColor" />
    </div>
    <div class="form-group row">
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               data-valid="['required']"
               v-model="$parent.activeItem.fontSize"
               placeholder="14px" />
      </div>
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               v-model="$parent.activeItem.radius"
               placeholder="14px" />
      </div>
    </div>
    <div class="form-group row">
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               data-valid="['required','num']"
               v-model="$parent.activeItem.height" />
      </div>
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               data-valid="['required','num']"
               v-model="$parent.activeItem.width" />
      </div>
    </div>
    <div class="form-group row">
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               data-valid="['required','num']"
               v-model="$parent.activeItem.translateY" />
      </div>
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               data-valid="['required','num']"
               v-model="$parent.activeItem.translateX" />
      </div>
    </div>
    <div class="form-group row">
      <div class="col">
        <label></label>
        <input type="text"
               class="form-control"
               data-valid="['num']"
               placeholder="2"
               v-model="$parent.activeItem.zIndex" />
      </div>
    </div>
  </form>
</template>
<script>
export default {
    methods: {
        changeBtnType: function(e) {
            this.$parent.activeItem.btnType = e
            this.$parent.activeItem.href = ''
        }
    },
    mounted() {
        const self = this
        //
        $('.colorInput').simpleColor({
            boxWidth: '100%',
            boxHeight: 39,
            cellWidth: 20,
            cellHeight: 20,
            livePreview: true,
            displayCSS: {
                border: '1px solid #cccccc'
            },
            onSelect(hex) {
                self.$parent.activeItem.color = hex
                $('.item-little-actived').css({
                    color: hex
                })
            }
        })
        //
        $('.bgColorInput').simpleColor({
            boxWidth: '100%',
            boxHeight: 39,
            cellWidth: 20,
            cellHeight: 20,
            livePreview: true,
            displayCSS: {
                border: '1px solid #cccccc'
            },
            onSelect(hex) {
                self.$parent.activeItem.bgColor = hex
                $('.item-little-actived').css({
                    'background-color': hex
                })
            }
        })
        //
        $('.borderColorInput').simpleColor({
            boxWidth: '100%',
            boxHeight: 39,
            cellWidth: 20,
            cellHeight: 20,
            livePreview: true,
            displayCSS: {
                border: '1px solid #cccccc'
            },
            onSelect(hex) {
                self.$parent.activeItem.borderColor = hex
                $('.item-little-actived').css({
                    border: '1px solid ' + hex
                })
            }
        })
    }
}
</script>
